@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap&subset=cyrillic-ext');
/*___main wrapper styles___*/
.mainWrapper {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
/*___form wrapper styles___*/
.wrapperLoginForm{
    width: 510px;
    height: 520px;
    border: 1px solid rgba(39, 39, 39, 0.1);
    box-sizing: border-box;
    border-radius: 5px;
    display: grid;
    grid-template-rows: 1fr
    1fr;
    justify-content: center;
}
/*___headline styles___*/
.headline{
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
}

.headline__text{
    margin-top: 32px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
}
/*___form styles___*/
.loginForm {
    max-width: 300px;
}






